<template>
	<view class="goods_detail">
		<swiper indicator-dots class="swiper">
			<swiper-item v-for="(item,index) in swipers" :key="index">
				<image class="img" :src="item.imgUrl"></image>
				<!-- <image class="img" :src="item.carouselUrl"></image> -->
				<!-- <text v-if="flag">数据暂时还没录入哦~</text> -->
			</swiper-item>
		</swiper>
		<view class="box1">
			<view class="price">
				<text>￥{{goodsInfo.presendPrice}}</text>
				<text>￥{{goodsInfo.originalPrice}}</text>
			</view>
			<view class="goods_name">
				商品名称:{{goodsInfo.goodsInfo}}
			</view>
		</view>
		<view class="line"></view>
		<view class="box2">
			<view>货号：SD59810321</view>
			<view>库存：200</view>
		</view>
		<view class="line"></view>
		<view class="box3">
			<view class="tit">详情介绍</view>
			<view class="content">
				<rich-text :nodes="content.contentString"></rich-text>
			</view>
		</view>
		<view class="goods_nav">
			<uni-goods-nav :fill="true" :options="options" :buttonGroup="buttonGroup" @click="onClick"
				@buttonClick="buttonClick" />
		</view>
	</view>
</template>

<script>
	import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue'

	export default {
		data() {
			return {
				id: 0,
				swipers: [],
				goodsInfo: [],
				content: {},
				options: [{
					icon: 'headphones',
					text: '客服'
				}, {
					icon: 'shop',
					text: '店铺',
					info: 0,
					infoBackgroundColor: '#007aff',
					infoColor: "red"
				}, {
					icon: 'cart',
					text: '购物车',
					info: 12
				}],
				buttonGroup: [{
						text: '加入购物车',
						backgroundColor: '#ff0000',
						color: '#fff'
					},
					{
						text: '立即购买',
						backgroundColor: '#ffa200',
						color: '#fff'
					}
				]
			}
		},
		methods: {
			async getSwipers() {
				const res = await this.$myRequest({
					url: '/getGoodsDetail/' + this.id
				})
				console.log(this.swipers)
				if (res.data.length > 0) {
					this.swipers = res.data
				}

			},
			async getGoodsDetail() {
				const res = await this.$myRequest({
					url: '/findByGoodsId/' + this.id
				})
				this.goodsInfo = res.data
				console.log(res.data)
			},
			async getGoodsDetailContent() {
				const res = await this.$myRequest({
					url: "/getContent/" + this.id
				})
				console.log(res.data)
				this.content = res.data[0]

			},
			onClick(e) {
				// uni.showToast({
				// 	title: `点击${e.content.text}`,
				// 	icon: 'none'
				// })
				console.log(e)
			},
			buttonClick(e) {
				console.log(e)
				// this.options[2].info++
			}
		
	},
	onLoad(options) {
			console.log(options)
			this.id = options.id
			this.getSwipers()
			this.getGoodsDetail()
			this.getGoodsDetailContent()
		},
		components: {
			"uniGoodsNav": uniGoodsNav
		}
	}
</script>

<style lang="scss">
	.swiper {

		height: 700rpx;

		swiper-item {

			.img {
				width: 100%;
				height: 100%;
			}
		}
	}

	.box1 {
		padding: 10px;

		.price {
			font-size: 35rpx;
			color: $shop-color;
			line-height: 80rpx;

			text:nth-child(2) {
				color: #ccc;
				font-size: 28rpx;
				text-decoration: line-through;
				margin-left: 20rpx;
			}
		}

		.goods_name {
			font-size: 32rpx;
			line-height: 60rpx;
			font-weight: bold;
		}
	}

	.line {
		height: 10rpx;
		width: 750rpx;
		background: #eee;
	}

	.box2 {
		font-size: 32rpx;
		line-height: 70rpx;
	}

	.box3 {
		padding-bottom: 100rpx;
		.tit {
			font-size: 32rpx;
			padding-left: 10px;
			border-bottom: 1px solid #eee;
			line-height: 70rpx;
		}

		.content {
			padding: 10px;
			font-size: 28rpx;
			color: #333333;
			line-height: 45rpx;
		}
	}
	.goods_nav{
		position: fixed;
		bottom: 0;
		width: 100%;
	}
</style>
